@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,300;0,400;0,600;0,700;0,900;1,300;1,400;1,600;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Anonymous+Pro&display=swap);
body {
  margin: 0;
  font-family: 'Source Sans Pro', serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-family: 'Source Sans Pro', sans-serif;
  display: block;
  font-size: 2.5em;
  font-weight: 300;
}


.side-bar {
  z-index: 0;
  display: flex;
  flex-wrap: wrap;
  background-color: #353;
}

.side-nav {
  color: white;
  display: block;
  padding-top: 3rem;
  padding-bottom: 1rem;
  overflow-y: auto;
  flex: 1 1 auto;
}

.nav-list {
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
  font-size: 18px;
}

.nav-list li {
  cursor: pointer;
  padding: 5px 100px 5px 20px;
  transition: 0.2s ease;
}

.nav-list li:hover {
  background-color: #457545;
}

.selected-nav {
  background-color: #457545;
}

@media (min-width: 50rem) {
  .side-bar {
    flex-wrap: nowrap;
    position: fixed;
    width: 248px;
    height: 100%;
    flex-direction: column;
    align-items: flex-end;
  }
}

@media (min-width: 66.5rem) {
  .side-bar {
    width: calc((100% - 1064px) / 2 + 264px);
    min-width: 264px;
  }
}

.main-content {
  padding: 2rem;
}

@media (min-width: 50rem) {
  .main {
    position: relative;
    max-width: 800px;
    margin-left: 248px;
  }
}

@media (min-width: 66.5rem) {
  .main {
    margin-left: calc( (100% - 1064px) / 2 + 264px );
  }
}
.data-table {
  font-family: 'Anonymous Pro', monospace;
  font-size: 18px;
  color: #333;
  border-spacing: 0;
  text-align: right;
}

.data-table thead tr td {
  background-color: #006400;
  color: white;
  padding: 5px 5px 5px 15px;
}

.data-table tbody tr td {
  padding: 2px 10px 2px 20px;
}

.data-table tbody tr td:nth-child(odd) {
  background-color: #ddeedd;
}

.button {
  font-weight: 600;
  display: inline-block;
  cursor: pointer;
  margin: 10px 10px 20px 10px;
  padding: 8px 15px;
  border-radius: 5px;
  border: none;
  transition: ease 0.2s;
}

.action-button {
  background-color: #006400;
  color: white;
}

.action-button:hover {
  background-color: #208420;
}

.toggle-button {
  background-color: white;
  color: #333;
  border: 2px solid #ddeedd;
}

.toggle-button:hover {
  background-color: #ddeedd;
}

.toggle-button.selected {
  background-color: #ddeedd;
  color: black;
}

.input-table tr td:nth-child(even) {
  padding-right: 40px;
}

.input-label {
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  color: #555;
}

.number-input {
  font-family: 'Anonymous Pro', monospace;
  font-size: 20px;
  padding: 5px 10px;
  margin: 5px 10px;
  width: 100px;
  border: 1px solid #aaa;
  border-radius: 3px;
}

.select-input {
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 16px;
  padding: 5px 10px;
  margin: 5px 10px;
  width: 122px;
  border: 1px solid #aaa;
  border-radius: 3px;
}



.key-stats tr td:nth-child(1) {
  font-size: 20px;
  font-weight: 700;
  text-transform: uppercase;
  color: #006400;
}

.key-stats tr td:nth-child(2) {
  font-family: 'Anonymous Pro', monospace;
  font-size: 36px;
  padding-left: 20px;
  color: #333;
}

